/* Custom checkbox styles */
.custom-checkbox .form-check-input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 20px;
    height: 20px;
    border: 2px solid #ccc;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
}
  
/* Custom checkbox styles - checked */
.custom-checkbox .form-check-input[type="checkbox"]:checked {
    background-color: #363a7a; /* Change this color to your desired fill color */
}

.custom-checkbox .form-check-input[type="checkbox"]:focus {
    border-color: #007bff; /* Change the border color to highlight when focused */
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25); /* Add a shadow effect when focused */
}
  
/* Custom checkbox styles - checked, before the checkmark */
.custom-checkbox .form-check-input[type="checkbox"]:checked::before {
    content: '\2713'; /* Unicode checkmark character */
    display: block;
    text-align: center;
    line-height: 15px;
    color: #fff; /* Color of the checkmark */
}
  
